import { useEffect, useState } from 'react';
import WalletConnect from './components/WalletConnect';
import { ethers } from 'ethers';
import './App.css'

import contractABI from './abi/contractABI.json'
// import contractByteCode from './abi/bytecode.js'

const contractAddress = '0x607bAd390E3aD81d8137775bc35362F7cdfE89C6'

function App() {

  const [provider, setProvider] = useState(null);
  const [signer, setSigner] = useState(null);
  const [contract, setContract] = useState(null);
  const [defaultAccount, setDefaultAccount] = useState(null);

  //
  const [name, setName] = useState(null);
  const [symbol, setSymbol] = useState(null);
  const [supply, setSupply] = useState(0);
  // const [decimal, setDecimal] = useState(0);

  const handleAccountChange = (value) => {
    setDefaultAccount(value);
  }
  const deployFee = ethers.utils.parseEther("0.03")
  const gasPrice = ethers.utils.parseUnits("0.25", "gwei");


  const updateEthers = async () => {
    try {
      let tempProvider = new ethers.providers.Web3Provider(window.ethereum);
      setProvider(tempProvider);

      let tempSigner = tempProvider.getSigner();
      setSigner(tempSigner);

      let tempContract = new ethers.Contract(contractAddress, contractABI, tempSigner)
      setContract(tempContract);
    } catch {

    }
  }

  const deployContract = async () => {
    // console.log(contract)
    const realSupply = ethers.utils.parseEther(supply);
    const gasLimit = '10000000'
    try {
      let result = await contract.createToken(name, symbol, realSupply, {
        value: deployFee,
        gasPrice: gasPrice,
        gasLimit: gasLimit,
      });
      console.log(result)
    } catch (error) {
      console.log(error)
    }
  }

  useEffect(() => {
    updateEthers()
  }, [defaultAccount])

  const texts = [
    {
      title: 'Name',
      placeholder: 'Contract Name',
      function: (e) => setName(e.target.value),
    },
    {
      title: 'Symbol',
      placeholder: 'Contract Symbol',
      function: (e) => setSymbol(e.target.value),
    },
    {
      title: 'Supply',
      placeholder: 'Contract Supply',
      function: (e) => setSupply(e.target.value),
    },
    // {
    //   title: 'Decimal',
    //   placeholder: 'Contract Decimal',
    //   function: (e) => {
    //     if (e.target.value > 18) {
    //       alert("You can not set decimal higher than 18", "error")
    //       setDecimal(18)
    //       return;
    //     }
    //     if (e.target.value < 0) {
    //       alert("You can not set decimal lower than 0", "error")
    //       setDecimal(0)
    //       return;
    //     }
    //     setDecimal(e.target.value)
    //   },
    // },
  ]

  return (
    <div className="App" style={{
      width: '100vw', height: '100vh',
      display: 'flex', alignItems: 'center', justifyContent: 'center'
    }}>
      <div style={{
        padding: '30px',
        border: '1px solid lightblue',
        borderRadius: '20px'
      }}>
        <div className='WalletBtnContainer' style={{
          // display: 'flex', flexDirection: 'row',
          position: 'fixed', top: '20px', right: '20px'
        }}>
          <WalletConnect defaultAccountChange={handleAccountChange} />
        </div>
        <div className='ContentContainer' style={{
          width: '80vw',
          height: '60vh',
          borderRadius: '20px',
          // border: '1px solid black',
          display: 'flex', flexDirection: 'column',
          alignItems: 'center', justifyContent: 'center'
        }}>
          <h3>
            zkSync Token Creator <br />
            <span style={{ color: 'red' }}>0.03 ETH</span>
          </h3>
          <div style={{
            // position: 'fixed', marginTop: '20vh', 
            // paddingTop:'5vh',
            color: 'deepskyblue', fontSize: '30px', fontWeight: 'bold',
          }}
          >
            {
              texts.map((text, index) => {
                return (

                  <div className='row'
                    style={{
                      display: 'flex', flexDirection: 'row',
                      paddingTop: '5vh',
                      justifyContent: 'space-between'
                    }}
                    key={index}
                  >
                    <span>
                      {text.title}
                    </span>
                    <input placeholder={text.placeholder}
                      style={{
                        borderRadius: '10px', height: '40px',
                        marginLeft: '20px', paddingLeft: '20px',
                        border: '2px solid lightblue',
                        outlineColor: 'red',
                      }}
                      onChange={text.function}
                    />
                  </div>
                )
              })
            }
          </div>

          <button
            onClick={() => {
              deployContract()
            }}
            style={{
              marginTop: '5vh',
            }}
          >
            Deploy 0 Tax Contract
          </button>
        </div>
      </div>
    </div>
  );
}

export default App;
